html {
  scroll-behavior: smooth;
  max-width: 100%;
}

body {
  font-family: Jost, sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  background-color: #f7f8fd;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}

bold {
  font-weight: 700;
}

button,
input,
summary {
  border: 0;
  outline: 0;
  padding: 0;
  background: unset;
  background-color: unset;
  color: unset;
}
button:active,
input:active,
summary:active {
  border: 0;
  outline: 0;
}
button:focus,
input:focus,
summary:focus {
  border: 0;
  outline: 0;
}

button {
  cursor: pointer;
}

p,
h1,
h2,
h3,
h4,
h5 {
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
  padding-block-start: 0;
  padding-block-end: 0;
  padding-inline-start: 0;
  padding-inline-end: 0;
}

img,
embed {
  max-width: 100%;
  height: auto;
}

figure {
  display: unset;
  margin-block-start: unset;
  margin-block-end: unset;
  margin-inline-start: unset;
  margin-inline-end: unset;
}

a {
  text-decoration: none;
  border: 0px solid black;
  outline: none;
  color: unset;
}

a:any-link {
  border: none;
}

a:-webkit-any-link {
  border: none;
}

address {
  font-style: unset;
}

.tabFocus:focus-visible {
  outline: 0.2vw solid rgba(15, 15, 15, 0.575);
  outline-offset: 0.2vw;
  transition: 0s all;
}

*:focus {
  border: 0;
  outline: 0;
}

ul,
li,
ol {
  display: block;
  list-style-type: unset;
  margin-block-start: unset;
  margin-block-end: unset;
  margin-inline-start: unset;
  margin-inline-end: unset;
  padding-inline-start: unset;
}

input,
select,
.back,
a,
button {
  cursor: pointer;
}

.header {
  background-image: url("/src/assets/mobile/bg-pattern-header.svg");
  padding: 0 6.4vw;
  /* Rounded sliders */
}
@media screen and (min-width: 768px) {
  .header {
    background-image: url("/src/assets/tablet/bg-pattern-header.svg");
  }
}
@media screen and (min-width: 1280px) {
  .header {
    background-image: url("/src/assets/desktop/bg-pattern-header.svg");
  }
}
.header .wrapper {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  height: 36.2666666667vw;
}
@media screen and (min-width: 768px) {
  .header .wrapper {
    height: 160px;
  }
}
.header .switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}
.header .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.header .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.header .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.header input:checked + .slider {
  background-color: #2196f3;
}
.header input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}
.header input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
.header .slider.round {
  border-radius: 34px;
}
.header .slider.round:before {
  border-radius: 50%;
}

.jobs {
  display: flex;
  justify-content: center;
  padding: 0 6.4vw;
}
.jobs .wrapper {
  width: 100%;
}
.jobs .wrapper .list-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: safe;
}
.jobs .wrapper .list-wrapper .list-item {
  flex: 100%;
  margin: 20px 0;
  border-radius: 5px;
}
@media screen and (min-width: 768px) {
  .jobs .wrapper .list-wrapper .list-item {
    flex: 50%;
    height: 253px;
  }
}
@media screen and (min-width: 1280px) {
  .jobs .wrapper .list-wrapper .list-item {
    flex: 0 0 33%;
  }
}
.jobs .wrapper .list-wrapper .list-item .img-container .img1 {
  display: block;
  height: 50px;
  width: 50px;
  background-repeat: no-repeat;
  background-size: calc(100% - 20px);
  background-position: center;
  border-radius: 15px;
}
.jobs .wrapper .list-wrapper .list-item .img-container + div {
  margin-top: 16px;
}
.jobs .wrapper .list-wrapper .list-item h4 {
  margin-top: 24px;
}
.jobs .wrapper .list-wrapper .list-item h4 + div {
  margin-top: 16px;
}
.jobs .wrapper .list-wrapper .list-item strong {
  display: block;
  margin-top: 16px;
}

