.header {
  background-image: url("/src/assets/mobile/bg-pattern-header.svg");
  padding: 0 vwm(24);

  @include tablet {
    background-image: url("/src/assets/tablet/bg-pattern-header.svg");
  }

  @include desktop {
    background-image: url("/src/assets/desktop/bg-pattern-header.svg");
  }

  & .wrapper {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    height: vwm(136);

    @include tablet {
      height: 160px;
    }
  }

  .switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  input:checked + .slider {
    background-color: #2196f3;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px #2196f3;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }

  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }
}
